import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-vertical-nav-bar',
  templateUrl: './vertical-nav-bar.component.html',
  styleUrls: ['./vertical-nav-bar.component.css']
})
export class VerticalNavBarComponent implements OnInit {

  buttons: object[];

  constructor() { }

  ngOnInit(): void {
    this.buttons = [
      {
        title: "Consulta General",
        text: "Propiedades, mandatos, contratos",
        icon: "assets/icon/search.svg",
        selected: false,
        accent: "#FF4A1C"
      },
      {
        title: "Informes",
        text: "Generar informes",
        icon: "assets/icon/marketing.svg",
        selected: false,
        accent: "#F03A47"
      },
      {
        title: "Parametros",
        text: "Parametros globales del sistema",
        icon: "assets/icon/gear.svg",
        selected: false,
        accent: "#379634"
      }
    ];
  }

  getButtonClass(button){
    return {
      'nav-selected': button.selected
    }
  }

  onButtonClick(button){
    button.selected = !button.selected;
  }

}
